@keyframes bounce {
  0%, 50%, 100% {
    opacity: 1;
  }
  25%, 75% {
    opacity: 0;
  }
}
@keyframes blink {
  0%, 50%, 100% {
    opacity: 1;
  }
  25%, 75% {
    opacity: 0;
  }
}
:export {
  fadeTime: 500ms;
}

.root .dots {
  position: absolute;
  right: 10%;
  top: 0;
}
.root .dots ul {
  margin: 0;
  padding: 0 0 0 1rem;
  list-style: none;
  background-color: #2a2a2a;
  transform: translateY(15px);
}
.root .dots ul li {
  display: inline-block;
  margin-left: 0.5rem;
}
.root .dots ul li a {
  display: block;
  width: 13px;
  height: 13px;
  border-radius: 6px;
  background-color: #e1e1e1;
  font-size: 0;
}
.root .dots ul li a:hover {
  background-color: #d58e32;
}
.root .leftColumn {
  height: 70vh;
  display: block;
  border: 5px solid lightgray;
  width: 100%;
}
.root .leftColumn .header {
  background-color: #2a2a2a;
}
.root .leftColumn .header h3 {
  color: #e2e2e2;
  text-transform: uppercase;
  font-size: 20px;
  line-height: 38px;
  margin: 0;
  letter-spacing: 1px;
  padding-left: 3%;
  width: 50%;
}
.root .leftColumn .photo1:hover .addCart {
  visibility: visible;
}
.root .leftColumn .photo1:hover .time {
  visibility: visible;
}
.root .leftColumn .photo1 .pic1 {
  z-index: 1;
  width: 100%;
  height: 45vh;
}
.root .leftColumn .photo1 .addCart {
  visibility: hidden;
}
.root .leftColumn .photo1 .addCart .addToCartBtn {
  position: absolute;
  top: 40%;
  left: 50%;
  margin-right: -50%;
  transform: translate(-50%, -50%);
}
.root .leftColumn .photo1 .time {
  position: absolute;
  top: 60%;
  left: 45%;
  margin-right: -50%;
  transform: translate(-50%, -50%);
  width: 100%;
  visibility: hidden;
}
.root .leftColumn .photo1 .time li {
  display: inline-block;
  background-color: #434343;
  margin: 10px;
  text-align: center;
  border-radius: 50%;
  width: 65px;
  height: 65px;
}
.root .leftColumn .photo1 .time li h5 {
  font-size: 20px;
  color: #e2e2e2;
  font-weight: 700;
  padding-top: 5px;
}
.root .leftColumn .photo1 .time li p {
  color: #e2e2e2;
  font-size: 12px;
  padding-bottom: 0;
  margin: 0;
}
.root .leftColumn .background {
  background-color: #e2e2e2;
}
.root .leftColumn .outlines {
  position: absolute;
  top: 90%;
}
.root .leftColumn .outlines .option {
  margin-right: 8px;
  margin-left: 8px;
}
.root .leftColumn .priceButton {
  position: relative;
  right: 20px;
}
.root .leftColumn .price {
  position: absolute;
  top: 85%;
  left: 80%;
  display: inline-block;
}
.root .leftColumn .oldPrice {
  position: relative;
  right: 70px;
  top: 30px;
}
@media (max-width: 1200px) {
  .root .leftColumn .oldPrice {
    right: 74px;
  }
  .root .leftColumn .priceButton {
    position: relative;
    right: 20px;
  }
}
.root .leftColumn .line {
  margin: 0 10px;
  border-bottom: 1px solid #2a2a2a;
  position: relative;
  margin-top: 15%;
}
.root .leftColumn .line::before {
  content: "";
  position: absolute;
  bottom: -1px;
  left: 50%;
  width: 30px;
  border-bottom: 4px solid #d58e32;
  transform: translateX(-50%);
}
.root .leftColumn .name {
  text-align: center;
  font-size: 20px;
}
.root .leftColumn .stars {
  position: absolute;
  top: 82%;
  left: 50%;
  margin-right: -50%;
  transform: translate(-50%, -50%);
}
.root .leftColumn .stars a {
  padding: 5px;
  font-size: 20px;
  color: #2a2a2a;
}
.root .rightColumn {
  height: 70vh;
  width: 100%;
}
.root .rightColumn .pic2 {
  width: 100%;
  height: 500px;
}
.root .rightColumn .text {
  position: absolute;
  width: 96%;
  text-align: center;
  padding: 40px 40px;
  background-color: #434343;
  opacity: 0.6;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.root .rightColumn .text h2 {
  color: #e2e2e2;
  font-size: 50px;
  font-weight: 600;
}
.root .rightColumn .text h2 span {
  font-weight: 200;
}
.root .rightColumn .text p {
  color: #e2e2e2;
}
.root .rightColumn .shop {
  position: absolute;
  top: 68%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.root .rightColumn .shopBtn {
  background-color: #e2e2e2;
  padding: 15px;
}
.root .rightColumn .shopBtn span {
  color: #2a2a2a;
  font-weight: 600;
}
.root .rightColumn .buttons {
  position: absolute;
  width: 96%;
  position: absolute;
  top: 95%;
  left: 50%;
  transform: translate(-50%, -50%);
  display: inline-block;
}
.root .rightColumn .buttons .left {
  width: 45%;
}
.root .rightColumn .buttons .right {
  width: 49%;
}
@media (max-width: 1200px) {
  .root .rightColumn .buttons {
    left: 75%;
  }
  .root .rightColumn .buttons .left {
    width: 20%;
  }
  .root .rightColumn .buttons .right {
    width: 20%;
  }
  .root .rightColumn .buttons .oldPrice {
    right: 74px;
  }
}