@keyframes bounce {
  0%, 50%, 100% {
    opacity: 1;
  }
  25%, 75% {
    opacity: 0;
  }
}
@keyframes blink {
  0%, 50%, 100% {
    opacity: 1;
  }
  25%, 75% {
    opacity: 0;
  }
}
:export {
  fadeTime: 500ms;
}

.photoContainer {
  position: relative;
}

.heading {
  position: relative;
  margin-bottom: 20px;
}
.heading h3 {
  color: #d58e32;
  text-transform: uppercase;
  font-size: 20px;
  line-height: 38px;
  margin: 0;
  letter-spacing: 1px;
}
.heading::after {
  content: "";
  position: absolute;
  bottom: -4px;
  left: 0;
  width: 45px;
  border-bottom: 4px solid #d58e32;
}
.heading::before {
  content: "";
  position: absolute;
  bottom: -4px;
  left: 0;
  width: 100%;
  border-bottom: 4px solid #e2e2e2;
}

.photo {
  max-width: 100%;
  height: 100%;
}

.buttons {
  display: flex;
  position: absolute;
  flex-direction: column;
  top: 50%;
  transform: translateY(-30%);
  margin-left: 15px;
}
.buttons a {
  background-color: white;
  color: black;
  margin: 5px;
}

.priceBox {
  position: absolute;
  right: 15%;
  bottom: 15%;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.price {
  height: 150px;
  width: 150px;
  border-radius: 50%;
  background-color: black;
  opacity: 0.5;
  position: absolute;
  bottom: 75%;
  display: flex;
  align-items: center;
  justify-content: center;
  color: white;
  z-index: 1;
  flex-direction: column;
}

.slider .photo {
  max-width: 100px;
}

.name {
  background-color: white;
  height: 125px;
  width: 200px;
  overflow: hidden;
  display: flex;
  align-items: center;
  justify-content: center;
  display: flex;
  flex-direction: column;
}
.name:after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  border-top: 40px solid black;
  border-right: 40px solid white;
  width: 0;
}
.name:before {
  content: "";
  position: absolute;
  bottom: 0;
  right: 0;
  border-top: 40px solid white;
  border-right: 40px solid black;
  width: 0;
}

.menu {
  text-align: center;
}
.menu ul {
  margin: 0;
  padding: 0;
  list-style: none;
}
.menu ul li {
  display: inline-block;
  margin: 0 0.5rem;
  font-weight: 600;
}
.menu ul li a {
  color: rgb(42, 42, 42);
  position: relative;
  text-decoration: none;
  font-size: 18px;
  display: block;
}
.menu ul li a.active {
  color: #d58e32;
  border-top: 4px solid #d58e32;
}
.menu ul li a:hover {
  color: #d58e32;
}

.promotion span {
  color: rgb(65, 207, 65);
  font-size: 40px;
  font-weight: bold;
}
.promotion p {
  color: #d58e32;
  font-size: 40px;
  font-weight: bold;
}
.promotion a {
  background-color: green;
}
.promotion .promotionBox {
  position: absolute;
  top: 10%;
  left: 15%;
}

.sliderWrapper {
  margin-top: 10px;
  display: flex;
}
.sliderWrapper a {
  padding: 0 10px;
  display: flex;
  align-items: center;
  margin: 0 5px;
}

.slider {
  width: 100%;
  display: flex;
  overflow-x: hidden;
}
.slider img {
  width: 14.5%;
  margin: 0 5px;
  object-fit: cover;
}
.slider img.active {
  opacity: 0.5;
}

.stars a {
  text-decoration: none;
  color: rgb(42, 42, 42);
}

.oldPrice {
  text-decoration: line-through;
}

@media (max-width: 1000px) {
  .price {
    width: 80px;
    height: 80px;
  }
  .name {
    height: 100px;
    width: 175px;
  }
  .buttons {
    margin-left: 10px;
    top: 40%;
  }
  .buttons a {
    margin: 3px;
  }
  .stars a {
    width: 5px;
    height: 5px;
  }
}
@media (max-width: 768px) {
  .offer {
    visibility: hidden;
  }
}
@media (max-width: 320px) {
  .price {
    width: 50px;
    height: 50px;
  }
  .name {
    height: 70px;
    width: 100px;
  }
  .buttons {
    margin-left: 5px;
    top: 40%;
  }
  .buttons a {
    margin: 1px;
  }
}