@keyframes bounce {
  0%, 50%, 100% {
    opacity: 1;
  }
  25%, 75% {
    opacity: 0;
  }
}
@keyframes blink {
  0%, 50%, 100% {
    opacity: 1;
  }
  25%, 75% {
    opacity: 0;
  }
}
:export {
  fadeTime: 500ms;
}

.brandsButton {
  width: 15px;
  background-color: #2a2a2a;
  border: none;
  text-align: center;
  margin-top: 15px;
  margin-bottom: 15px;
  padding-right: 15px;
}

.brandsIcon {
  z-index: 50;
  color: gold;
}