@keyframes bounce {
  0%, 50%, 100% {
    opacity: 1;
  }
  25%, 75% {
    opacity: 0;
  }
}
@keyframes blink {
  0%, 50%, 100% {
    opacity: 1;
  }
  25%, 75% {
    opacity: 0;
  }
}
:export {
  fadeTime: 500ms;
}

.root {
  background-color: #ffffff;
  border: 1px solid #e1e1e1;
  margin-bottom: 2rem;
}
.root:hover .photo .buttons {
  visibility: visible;
}
.root:hover .actions div:last-child {
  background-color: #d58e32;
}
.root .photo {
  position: relative;
  padding: 4% 10px 0 10px;
  background-color: #f4f5f7;
  background-size: contain;
  background-position: center;
  background-repeat: no-repeat;
  overflow: hidden;
  align-items: flex-end;
}
.root .photo .sale {
  position: absolute;
  top: -30px;
  left: 50%;
  transform: translateX(-50%);
  background-color: #292929;
  border-radius: 50%;
  color: #ffffff;
  padding: 30px 15px 5px 15px;
}
.root .photo .buttons {
  display: flex;
  visibility: hidden;
  justify-content: space-between;
}
.root .photo .buttons a {
  text-align: center;
}
.root .photo .buttons a:first-child {
  margin-right: 1px;
}
.root .photo img {
  height: 300px;
  width: 100%;
  object-fit: cover;
}
.root .content {
  text-align: center;
  padding: 20px;
}
.root .content h5 {
  color: #d58e32;
  font-size: 16px;
  line-height: 24px;
  letter-spacing: 1px;
}
.root .line {
  margin: 0 10px;
  border-bottom: 1px solid #2a2a2a;
  position: relative;
}
.root .line::before {
  content: "";
  position: absolute;
  bottom: -1px;
  left: 50%;
  width: 30px;
  border-bottom: 4px solid #d58e32;
  transform: translateX(-50%);
}
.root .price {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.root .price .oldPrice {
  margin-right: 8px;
  text-decoration: line-through;
  color: #434343;
  opacity: 0.5;
}
.root .actions {
  padding: 15px 10px 10px 10px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.favorite,
.compare {
  color: #d58e32;
}

.popupModal .popupHeader {
  display: flex;
  align-items: center;
  padding: 0;
  background-color: #d58e32;
}
.popupModal .popupHeader * {
  padding: 5px;
  margin: 0 10px 0 0;
}
.popupModal .popupHeader button {
  order: -1;
}

.popupBody {
  background-color: #dfdfdf;
  display: flex;
  height: 300px;
}
.popupBody .popupImgBox {
  flex-basis: 50%;
  border: 2px solid #d58e32;
  padding: 5px;
}
.popupBody .popupImgBox .popupImg {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.popupBody .popupInfoBox {
  margin-left: 5px;
  padding: 5px;
  background-color: #faebd7;
  flex-basis: 50%;
  text-align: center;
  display: flex;
  align-items: center;
  justify-content: center;
}
.popupBody .popupInfoBox .popupOldPrice {
  text-decoration-line: line-through;
  color: #676767;
}