@keyframes bounce {
  0%, 50%, 100% {
    opacity: 1;
  }
  25%, 75% {
    opacity: 0;
  }
}
@keyframes blink {
  0%, 50%, 100% {
    opacity: 1;
  }
  25%, 75% {
    opacity: 0;
  }
}
:export {
  fadeTime: 500ms;
}

.root .panelBar {
  margin-bottom: 15px;
  position: relative;
}
.root .panelBar :global(.row) > * {
  border-bottom: 4px solid #e2e2e2;
}
.root .panelBar .heading h3 {
  position: relative;
  color: #d58e32;
  text-transform: uppercase;
  font-size: 20px;
  line-height: 38px;
  margin: 0;
  letter-spacing: 1px;
  z-index: 2;
}
.root .panelBar .heading::before {
  content: "";
  position: absolute;
  bottom: -4px;
  left: 0;
  width: 45px;
  border-bottom: 4px solid #d58e32;
}
.root .panelBar .dots {
  position: absolute;
  right: 0;
  top: 0;
  z-index: 1;
}
.root .panelBar .dots ul {
  margin: 0;
  padding: 0 0 0 1rem;
  list-style: none;
  background-color: #ffffff;
  transform: translateY(15px);
}
.root .panelBar .dots ul li {
  display: inline-block;
  margin-left: 0.5rem;
}
.root .panelBar .dots ul li a {
  display: block;
  width: 13px;
  height: 13px;
  border-radius: 6px;
  background-color: #e1e1e1;
  font-size: 0;
}
.root .panelBar .dots ul li a.active, .root .panelBar .dots ul li a:hover {
  background-color: #d58e32;
}

.feedbackBox {
  margin-bottom: 30px;
}
.feedbackBox .icon {
  font-size: 24px;
  line-height: 74px;
  color: #2a2a2a;
  text-align: center;
}
.feedbackBox .imgBox {
  width: 60px;
  height: 60px;
  border-radius: 5px;
  background-color: #676767;
  border: 1px solid #aaaaaa;
  object-fit: cover;
}
.feedbackBox .imgBox .img {
  width: 100%;
  height: 100%;
}
.feedbackBox .textBox {
  margin-bottom: 20px;
}
.feedbackBox .textBox p {
  font-size: 14px;
  line-height: 25px;
  color: #676767;
  text-align: center;
}
.feedbackBox .clientDataBox {
  display: flex;
  justify-content: center;
  margin: 20px auto 0 auto;
}
.feedbackBox .nameBox {
  width: 150px;
  display: flex;
  flex-wrap: wrap;
  margin-left: 20px;
}
.feedbackBox .nameBox p {
  flex-basis: 100%;
}
.feedbackBox .nameBox p:nth-child(1) {
  margin: 0;
  font-size: 16px;
  line-height: 24px;
  color: #676767;
  font-weight: 600;
  text-align: center;
}
.feedbackBox .nameBox p:nth-child(2) {
  font-size: 14px;
  margin: 0;
  line-height: 24px;
  color: #676767;
  text-align: center;
}