@charset "UTF-8";
@keyframes bounce {
  0%, 50%, 100% {
    opacity: 1;
  }
  25%, 75% {
    opacity: 0;
  }
}
@keyframes blink {
  0%, 50%, 100% {
    opacity: 1;
  }
  25%, 75% {
    opacity: 0;
  }
}
:export {
  fadeTime: 500ms;
}

h4 {
  border-bottom: 1px solid #2a2a2a;
  line-height: 1.8;
  font-size: 20px;
}

.container {
  display: flex;
  align-items: center;
  margin-bottom: 10px;
}
.container:hover {
  margin-left: 10px;
}

.container label {
  margin: 0;
  font-size: 20px;
  cursor: pointer;
  display: flex;
  align-items: center;
}

.container input[type=checkbox] {
  cursor: pointer;
  opacity: 0;
  display: flex;
  position: absolute;
}

.container label::before {
  content: "";
  width: 20px;
  height: 20px;
  border-radius: 20%;
  background-color: red;
  margin-right: 10px;
}

.container .green::before {
  background-color: green;
}

.container .pink::before {
  background-color: pink;
}

.container .blue::before {
  background-color: blue;
}

.container .black::before {
  background-color: black;
}

.container input[type=checkbox]:checked + label::before {
  content: "";
  color: white;
  display: flex;
  align-items: center;
  justify-content: center;
}