@charset "UTF-8";
@keyframes bounce {
  0%, 50%, 100% {
    opacity: 1;
  }
  25%, 75% {
    opacity: 0;
  }
}
@keyframes blink {
  0%, 50%, 100% {
    opacity: 1;
  }
  25%, 75% {
    opacity: 0;
  }
}
:export {
  fadeTime: 500ms;
}

.form {
  padding: 40px;
}
.form .radioBtns {
  display: flex;
  justify-content: space-between;
  font-size: 24px;
}
.form .radioBtns input {
  margin-right: 10px;
  appearance: none;
  font: inherit;
  color: #292929;
  width: 20px;
  height: 20px;
  border: 2px solid #292929;
  border-radius: 50%;
  transform: translateY(-3px);
  display: inline-grid;
  place-content: center;
}
.form .radioBtns input::before {
  content: "";
  width: 10px;
  height: 10px;
  border-radius: 50%;
  transform: scale(0);
  transition: 120ms transform ease-in-out;
  background-color: #292929;
}
.form .radioBtns input:checked::before {
  transform: scale(1);
}
.form .radioBtns label {
  margin: 0;
}
.form .userData {
  margin-top: 50px;
}
.form .userData input {
  display: block;
  margin-top: 25px;
  padding: 5px;
  width: 100%;
}
.form .switch {
  margin-top: 25px;
  display: block;
  text-align: right;
}
.form .checkboxBtns div {
  margin-bottom: 20px;
}
.form .checkboxBtns input {
  display: none;
}
.form .checkboxBtns input + *::before {
  margin-right: 15px;
  content: "";
  display: inline-block;
  vertical-align: middle;
  width: 20px;
  height: 20px;
  border-radius: 5px;
  border-style: solid;
  border-width: 2px;
  border-color: #292929;
  transform: translateY(-2px);
}
.form .checkboxBtns input:checked + *::before {
  content: "";
  color: #fff;
  line-height: 17px;
  text-align: center;
  background: #d58e32;
  border-color: #292929;
}
.form .checkboxBtns span {
  color: #d58e32;
}
.form small {
  color: red;
}
.form .formButtons {
  display: flex;
  align-items: center;
  margin-top: 40px;
  display: flex;
  justify-content: space-between;
}
.form .formButtons .previousBtn {
  border: none;
  color: #d58e32;
  background-color: transparent;
}
.form .formButtons .previousBtn:hover {
  color: #d47f10;
}
.form .formButtons .submitBtn {
  padding: 10px 15px;
  background-color: #d58e32;
  color: #fff;
  border: 2px solid #d58e32;
  border-radius: 5px;
}
.form .formButtons .submitBtn:hover {
  color: #d58e32;
  background-color: #fff;
  border: 2px solid #d58e32;
}