@keyframes bounce {
  0%, 50%, 100% {
    opacity: 1;
  }
  25%, 75% {
    opacity: 0;
  }
}
@keyframes blink {
  0%, 50%, 100% {
    opacity: 1;
  }
  25%, 75% {
    opacity: 0;
  }
}
:export {
  fadeTime: 500ms;
}

.root {
  padding: 24px 0;
}
.root .wrapper {
  border: solid 1px grey;
}
.root .wrapper .tableTabs ul {
  list-style-type: none;
  display: flex;
  flex-direction: row;
  padding-left: 0;
}
.root .wrapper .tableTabs ul li {
  padding: 15px 25px;
  font-weight: 500;
  border-right: solid 1px grey;
  border-bottom: solid 1px grey;
}
.root .wrapper .tableTabs ul li a {
  color: black;
  text-decoration: none;
}
.root .wrapper .tableTabs ul .active {
  border-bottom: none;
}
.root .wrapper .tableTabs ul .active a {
  color: #d58e32;
}
.root .wrapper .tableContent {
  margin: 30px 35px;
}
.root .wrapper .tableContent p {
  font-size: 14px;
  font-weight: 300;
  color: rgb(44, 44, 44);
}
.root .wrapper .tableContent .add {
  font-size: 15px;
  color: black;
  font-weight: 400;
  padding: 15px 0;
}
.root .wrapper .tableContent .rating {
  display: flex;
  flex-direction: row;
  padding-bottom: 10px;
}
.root .wrapper .tableContent .rating .stars {
  padding: 0 10px;
  font-size: 13px;
}
.root .wrapper .tableContent .rating .stars a {
  text-decoration: none;
  color: #d58e32;
}
.root .wrapper .tableContent form p {
  margin-bottom: 5px;
}
.root .wrapper .tableContent form .textArea {
  width: 100%;
  resize: none;
  border: solid 1px lightgrey;
  margin-bottom: 10px;
}
.root .wrapper .tableContent form .inputsWrapper {
  display: flex;
  justify-content: space-between;
  margin: 0 0 70px 0;
}
.root .wrapper .tableContent form .inputsWrapper input {
  border: solid 1px lightgrey;
  font-size: 14px;
  padding: 15px;
  width: 41%;
}
.root .wrapper .tableContent form .inputsWrapper .buttonContinue {
  display: flex;
  align-items: center;
}