@keyframes bounce {
  0%, 50%, 100% {
    opacity: 1;
  }
  25%, 75% {
    opacity: 0;
  }
}
@keyframes blink {
  0%, 50%, 100% {
    opacity: 1;
  }
  25%, 75% {
    opacity: 0;
  }
}
:export {
  fadeTime: 500ms;
}

.root .panelBar {
  margin-bottom: 30px;
  position: relative;
}
.root .panelBar :global(.row) > * {
  border-bottom: 4px solid #e2e2e2;
}
.root .panelBar .heading {
  position: relative;
}
.root .panelBar .heading h3 {
  color: #d58e32;
  text-transform: uppercase;
  font-size: 20px;
  line-height: 38px;
  margin: 0;
  letter-spacing: 1px;
}
@media (max-width: 600px) {
  .root .panelBar .heading h3 {
    text-align: center;
  }
}
.root .panelBar .heading::before {
  content: "";
  position: absolute;
  bottom: -4px;
  left: 0;
  width: 45px;
  border-bottom: 4px solid #d58e32;
}
@media (max-width: 600px) {
  .root .panelBar .heading::before {
    left: 50%;
    transform: translateX(-50%);
    width: 45px;
  }
}
.root .panelBar .menu {
  text-align: center;
}
.root .panelBar .menu ul {
  margin: 0;
  padding: 0;
  list-style: none;
}
.root .panelBar .menu ul li {
  display: inline-block;
  margin: 0 0.5rem;
  font-weight: 600;
}
.root .panelBar .menu ul li a {
  color: rgb(42, 42, 42);
  position: relative;
  text-decoration: none;
  font-size: 18px;
  display: block;
}
.root .panelBar .menu ul li a.active::before, .root .panelBar .menu ul li a:hover::before {
  content: "";
  position: absolute;
  bottom: -4px;
  left: 0;
  width: 100%;
  border-bottom: 4px solid #d58e32;
}
.root .panelBar .dots {
  position: absolute;
  right: 0;
  top: 0;
}
@media (max-width: 600px) {
  .root .panelBar .dots {
    position: relative;
  }
}
.root .panelBar .dots ul {
  margin: 0;
  padding: 0 0 0 1rem;
  list-style: none;
  background-color: #ffffff;
  transform: translateY(15px);
}
@media (max-width: 600px) {
  .root .panelBar .dots ul {
    padding: 0;
    text-align: center;
  }
  .root .panelBar .dots ul li:first-child {
    margin-left: 0;
  }
}
.root .panelBar .dots ul li {
  display: inline-block;
  margin-left: 0.5rem;
}
.root .panelBar .dots ul li a {
  display: block;
  width: 13px;
  height: 13px;
  border-radius: 6px;
  background-color: #e1e1e1;
  font-size: 0;
}
.root .panelBar .dots ul li a.active, .root .panelBar .dots ul li a:hover {
  background-color: #d58e32;
}

.fadeIn {
  animation: fadein 500ms;
}

.fadeOut {
  animation: fadeOut 500ms;
}

@keyframes fadein {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
@keyframes fadeOut {
  from {
    opacity: 1;
  }
  to {
    opacity: 0;
  }
}