@keyframes bounce {
  0%, 50%, 100% {
    opacity: 1;
  }
  25%, 75% {
    opacity: 0;
  }
}
@keyframes blink {
  0%, 50%, 100% {
    opacity: 1;
  }
  25%, 75% {
    opacity: 0;
  }
}
:export {
  fadeTime: 500ms;
}

.buttonChat {
  background-color: #d58e32;
  text-align: center;
  height: 50px;
  width: 150px;
  border-radius: 3px;
  display: flex;
  align-items: center;
  justify-content: center;
  color: white;
}
.buttonChat:hover {
  cursor: pointer;
  background-color: #d47f10;
}
.buttonChat.active {
  width: 100%;
  height: 100%;
  border-radius: 0;
}

.chat {
  position: fixed;
  bottom: 0;
  left: 25px;
}
.chat.active {
  bottom: 0;
  left: 0;
}
.chat .chatBox {
  max-height: 0;
  overflow: hidden;
  background-color: white;
}
.chat .fullChatBox {
  width: 300px;
  background-color: white;
  text-align: center;
  overflow: auto;
  bottom: 0;
}
.chat .chatBoxContainer {
  min-height: 500px;
  background-color: #dfdfdf;
  position: relative;
}
.chat .chatContainer {
  max-height: 500px;
  width: 100%;
  position: absolute;
}
.chat .chatContainer ul {
  list-style: none;
  padding: 0;
  display: flex;
  justify-content: center;
}
.chat .chatContainer li {
  background-color: white;
  width: 90%;
  border-radius: 5px;
  margin: 5px 0;
  box-shadow: rgba(0, 0, 0, 0.15) 1.95px 1.95px 2.6px;
}
.chat .message {
  display: flex;
  justify-content: space-between;
}
.chat .message a {
  width: 100%;
  background-color: #d58e32;
}
.chat .message a:hover {
  background-color: #d47f10;
}