@keyframes bounce {
  0%, 50%, 100% {
    opacity: 1;
  }
  25%, 75% {
    opacity: 0;
  }
}
@keyframes blink {
  0%, 50%, 100% {
    opacity: 1;
  }
  25%, 75% {
    opacity: 0;
  }
}
:export {
  fadeTime: 500ms;
}

.root {
  padding: 24px 0;
}

.section {
  display: flex;
  padding: 10px;
  margin: 10px;
  border: 2px solid #dfdfdf;
}

.productDetails {
  padding: 30px;
}

.price {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.price .oldPrice {
  margin-right: 8px;
  text-decoration: line-through;
  color: #434343;
  opacity: 0.5;
}

.largeImage {
  position: relative;
}
.largeImage .photoButton {
  position: absolute;
  bottom: 8px;
  right: 4px;
}

.smallGallery {
  position: relative;
}

.gallerySmallImage {
  margin: 10px 12px;
}

.smallGalleryButton, .smallGalleryButtonRight, .smallGalleryButtonLeft {
  position: absolute;
  top: 45px;
  margin-right: 20px;
}

.smallGalleryButtonLeft {
  margin-right: 40px;
}

.smallGalleryButtonRight {
  margin-left: -28px;
}

.titleSection {
  display: flex;
  justify-content: space-between;
  padding-top: 20px;
}

.priceSection {
  padding: 20px 0;
  border-bottom: 2px solid #dfdfdf;
}

.addToCartSection {
  padding: 20px 0;
}

.quantitySection {
  padding: 11px 0;
  border-bottom: 2px solid #dfdfdf;
}

.titleButtonSection {
  padding-top: 9px;
}

.favoriteSection {
  border-bottom: 2px solid #dfdfdf;
  padding: 20px 0;
}

.iconTransparent {
  background-color: #fff;
  border: 1px solid #676767;
  color: black;
  margin-right: 5px;
}

.iconTransparentSocial, .iconSocial, .iconSocialLinkedin, .iconSocialPinterest, .iconSocialTwitter, .iconSocialGoogle, .iconSocialYT, .iconSocialFacebook {
  background-color: #fff;
  border: 1px solid #676767;
  color: black;
  margin-right: 3px;
}

.quantitySection input {
  margin: 15px 5px 2px 15px;
}
.quantitySection button {
  margin-right: 4px;
}

.productQuantity {
  width: 65px;
  padding-bottom: 2px;
  text-align: center;
}

.overviewSection {
  padding: 20px 0;
  border-bottom: 2px solid #dfdfdf;
}

.availbilitySection {
  padding-top: 15px;
  border-bottom: 2px solid #dfdfdf;
}

.availbilityPoints {
  padding-top: 15px;
  padding-bottom: 15px;
  display: flex;
}

.socialSection {
  padding: 30px 0;
}
.socialSection button {
  margin-right: 10px;
}

.iconSocial, .iconSocialLinkedin, .iconSocialPinterest, .iconSocialTwitter, .iconSocialGoogle, .iconSocialYT, .iconSocialFacebook {
  font-size: 13px;
  line-height: 24px;
  padding: 3px 12px;
}

.facebook {
  color: #4267B2;
}

.iconSocialFacebook:hover .facebook {
  color: white;
}

.iconSocialFacebook:hover {
  background-color: #4267B2;
  color: white;
}

.youtube {
  color: #FF0000;
}

.iconSocialYT:hover .youtube {
  color: white;
}

.iconSocialYT:hover {
  background-color: #FF0000;
  color: white;
}

.google {
  color: #de5246;
}

.iconSocialGoogle:hover .google {
  color: white;
}

.iconSocialGoogle:hover {
  background-color: #de5246;
  color: white;
}

.twitter {
  color: #1DA1F2;
}

.iconSocialTwitter:hover .twitter {
  color: white;
}

.iconSocialTwitter:hover {
  background-color: #1DA1F2;
  color: white;
}

.pinterest {
  color: #E60023;
}

.iconSocialPinterest:hover .pinterest {
  color: white;
}

.iconSocialPinterest:hover {
  background-color: #E60023;
  color: white;
}

.linkedin {
  color: #E60023;
  text-align: center;
}

.iconSocialLinkedin:hover .pinterest {
  color: white;
}

.iconSocialLinkedin:hover {
  background-color: #0A66C2;
  color: white;
}