@keyframes bounce {
  0%, 50%, 100% {
    opacity: 1;
  }
  25%, 75% {
    opacity: 0;
  }
}
@keyframes blink {
  0%, 50%, 100% {
    opacity: 1;
  }
  25%, 75% {
    opacity: 0;
  }
}
:export {
  fadeTime: 500ms;
}

.root .footerMenu {
  background-color: #363636;
  padding: 3rem 0;
}
.root .footerMenu .menuWrapper h6 {
  color: #ffffff;
  text-transform: uppercase;
  margin: 0 0 1rem 0;
}
.root .footerMenu .menuWrapper ul {
  margin: 0 0 1.5rem 0;
  padding: 0;
  list-style: none;
}
.root .footerMenu .menuWrapper ul li a {
  font-size: 14px;
  line-height: 30px;
  color: rgb(169, 169, 169);
}
.root .footerMenu .menuWrapper ul li a:hover {
  color: #ffffff;
}
.root .footerMenu .paymentMethods {
  margin: 0;
  padding: 0;
  list-style: none;
}
.root .footerMenu .paymentMethods li {
  display: inline-block;
}
.root .bottomBar {
  background-color: #2a2a2a;
}
.root .bottomBar :global(.container) > :global(.row) {
  height: 95px;
}
.root .bottomBar .copyright p {
  margin: 0;
  color: rgb(169, 169, 169);
  font-size: 14px;
  line-height: 26px;
}
.root .bottomBar .socialMedia ul {
  margin: 0;
  padding: 0;
  list-style: none;
}
.root .bottomBar .socialMedia ul li {
  display: inline-block;
  margin-left: 1rem;
}
.root .bottomBar .socialMedia ul li a {
  color: rgb(169, 169, 169);
  text-decoration: none;
}
.root .bottomBar .socialMedia ul li a:hover {
  color: #d58e32;
}