@keyframes bounce {
  0%, 50%, 100% {
    opacity: 1;
  }
  25%, 75% {
    opacity: 0;
  }
}
@keyframes blink {
  0%, 50%, 100% {
    opacity: 1;
  }
  25%, 75% {
    opacity: 0;
  }
}
:export {
  fadeTime: 500ms;
}

.root {
  border: 1px solid #b6b6b6;
  text-align: center;
  margin-top: 40px;
}
.root .iconWrapper {
  height: 60px;
  transform: translateY(-50%);
  color: #2a2a2a;
  display: inline-flex;
  justify-content: center;
  align-items: center;
}
.root .iconWrapper .icon {
  position: relative;
  z-index: 9;
}
.root .iconWrapper::before {
  content: "";
  border: 1px solid #b6b6b6;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 76px;
  height: 76px;
  border-radius: 100%;
  background-color: #ffffff;
  z-index: -1;
}
.root .iconWrapper::after {
  content: "";
  width: 60px;
  height: 60px;
  border-radius: 100%;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  border: 1px solid #b6b6b6;
}
.root .content {
  text-transform: uppercase;
  color: rgb(42, 42, 42);
  margin-top: -0.5rem;
  letter-spacing: 1px;
  font-weight: 300;
}
.root .content h5 {
  font-weight: 600;
  margin: 0;
}
.root:hover .iconWrapper, .root.active .iconWrapper {
  color: #ffffff;
}
.root:hover .iconWrapper::after, .root.active .iconWrapper::after {
  background-color: #d58e32;
  border-color: #d58e32;
}
.root:hover a, .root.active a {
  text-decoration: none;
}
.root:hover .content, .root.active .content {
  color: #d58e32;
}