@keyframes bounce {
  0%, 50%, 100% {
    opacity: 1;
  }
  25%, 75% {
    opacity: 0;
  }
}
@keyframes blink {
  0%, 50%, 100% {
    opacity: 1;
  }
  25%, 75% {
    opacity: 0;
  }
}
:export {
  fadeTime: 500ms;
}

.root {
  padding: 20px 0;
  margin-bottom: 50px;
}
.root .topPage {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  background-color: #f5f6fa;
  padding: 25px 0;
}
.root .topPage h3 {
  margin: 0;
  margin-left: 100px;
  color: black;
}
.root .topPage .rightPage {
  display: flex;
  align-items: center;
  justify-content: space-around;
  margin-right: 100px;
  width: 5%;
  font-size: 13px;
}
.root .topPage .rightPage .icon {
  color: black;
}
.root .topPage .rightPage p {
  margin: 0;
  padding: 0;
}
.root .cart {
  width: 80%;
  margin: 50px auto;
}
.root .cart table {
  border-bottom: 1px solid #dcdde1;
  margin-bottom: 0;
  border-right: 1px solid #dcdde1;
  border-left: 1px solid #dcdde1;
}
.root .cart .tableBanner {
  background-color: #f5f6fa;
}
.root .cart .tableBanner th {
  border: none;
  font-size: 15px;
  color: black;
}
.root .cart .product {
  display: flex;
  align-items: center;
  padding: 20px;
  font-size: 14px;
  color: black;
  font-weight: normal;
}
.root .cart .product .remove {
  width: 25px;
  height: 25px;
  line-height: 25px;
  padding: 5px;
  border: solid 1px #dcdde1;
  color: #dcdde1;
  border-radius: 50%;
  margin-right: 50px;
}
.root .cart .product .remove:hover {
  cursor: pointer;
}
.root .cart .product .productImage {
  background-color: #dcdde1;
  width: 60px;
  height: 60px;
  margin-right: 80px;
}
.root .cart .product .productImage img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.root .cart td {
  padding: 20px;
  padding-right: 40px;
  color: #d47f10;
  font-size: 14px;
}
.root .cart td .quantity {
  width: 107.2px;
  display: flex;
}
.root .cart td .quantity .button1,
.root .cart td .quantity .button2 {
  border: solid 1px #dcdde1;
  margin: 0;
}
.root .cart td .quantity .button1:hover,
.root .cart td .quantity .button2:hover {
  background-color: #d58e32;
  text-decoration: none;
}
.root .cart td .quantity .number {
  border-top: solid 1px #dcdde1;
  border-bottom: solid 1px #dcdde1;
}
.root .cart td .quantity .number input {
  border: none;
  height: 33.6px;
  width: 44px;
}
.root .cart .couponCode {
  display: flex;
  align-items: center;
  border-right: solid 1px #dcdde1;
  border-left: solid 1px #dcdde1;
  border-bottom: solid 1px #dcdde1;
  padding: 20px 0;
}
.root .cart .couponCode input {
  margin: 0 5px;
  height: 29px;
  border: none;
  font-size: 14px;
  width: 18%;
  color: black;
  border: solid 1px #dcdde1;
  border-radius: 3px;
  outline: none;
}
.root .cart .couponCode .code,
.root .cart .couponCode .update {
  background-color: #d58e32;
  padding: 5px 30px;
}
.root .cart .couponCode .code:hover,
.root .cart .couponCode .update:hover {
  background-color: #d8ae77;
}
.root .cart .couponCode .update {
  margin-left: 55%;
}
.root .cartTotals {
  display: flex;
  flex-direction: column;
  width: 45%;
  margin-left: 45%;
  border: solid 1px #dcdde1;
  border-left: 1px solid #dcdde1;
}
.root .cartTotals h3 {
  background-color: #f5f6fa;
  text-align: center;
  padding: 15px 0;
  border-bottom: solid 1px #dcdde1;
  font-size: 15px;
  font-weight: bolder;
  color: black;
}
.root .cartTotals .subtotal,
.root .cartTotals .total {
  margin: 0 30px;
  height: 64px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  border-bottom: solid 1px #dcdde1;
}
.root .cartTotals .subtotal h5,
.root .cartTotals .subtotal p,
.root .cartTotals .total h5,
.root .cartTotals .total p {
  font-size: 14px;
  margin-bottom: 0;
  color: black;
}
.root .cartTotals .subtotal p,
.root .cartTotals .total p {
  padding: 10px 0 10px 10px;
  border-left: solid 1px #dcdde1;
  margin-right: 50%;
  color: #d47f10;
}
.root .cartTotals .checkout {
  margin: 10px 25px;
  padding: 10px;
  text-align: center;
  background-color: #d58e32;
  color: white;
  font-size: 14px;
}
.root .cartTotals .checkout:hover {
  text-decoration: none;
  background-color: #d8ae77;
}