@keyframes bounce {
  0%, 50%, 100% {
    opacity: 1;
  }
  25%, 75% {
    opacity: 0;
  }
}
@keyframes blink {
  0%, 50%, 100% {
    opacity: 1;
  }
  25%, 75% {
    opacity: 0;
  }
}
:export {
  fadeTime: 500ms;
}

.container {
  display: inline-block;
}

.searchInput input {
  padding: 10px;
  border: 2px solid #434343;
  float: left;
  width: 100%;
  border-radius: 3px;
  margin: 15px 15px 15px 0;
}

.blogUl {
  padding: 0px;
}

.blogUl li {
  list-style-type: none;
  padding: 10px;
  border-bottom: 1px solid gray;
}

.blogSection {
  max-height: 100%;
  color: #434343;
  z-index: -1;
}
.blogSection p {
  padding-top: 10px;
}

.sectionTitle {
  font-size: 16px;
  font-weight: bold;
}

.postTitle {
  font-size: 24px;
  font-weight: bold;
}

.blogImg {
  width: 100%;
  height: 70%;
}

.postInfoSection {
  display: flex;
  text-align: center;
}

.blogInfoSection {
  display: flex;
  border-top: 1px solid gray;
  border-bottom: 1px solid gray;
  align-items: center;
}

.readMoreSection p {
  margin: 0;
  padding-top: 5px;
  padding-bottom: 5px;
}