@keyframes bounce {
  0%, 50%, 100% {
    opacity: 1;
  }
  25%, 75% {
    opacity: 0;
  }
}
@keyframes blink {
  0%, 50%, 100% {
    opacity: 1;
  }
  25%, 75% {
    opacity: 0;
  }
}
:export {
  fadeTime: 500ms;
}

.bannerGroup {
  background-color: #363636;
  height: 200px;
  display: flex;
  color: white;
  justify-content: center;
  align-items: center;
  text-transform: uppercase;
  flex-direction: column;
  line-height: 27px;
}

.titleTop {
  font-size: 54px;
  font-weight: 300;
  padding-top: 27px;
}
.titleTop span {
  font-weight: bold;
}

.titleBottom {
  font-size: 26px;
  font-weight: 300;
  letter-spacing: 12px;
  padding-left: 9px;
}
.titleBottom .span {
  color: #d58e32;
  letter-spacing: 11px;
  font-weight: 500;
}
.titleBottom .alwaysSpan {
  letter-spacing: 9px;
}

.bottomBar {
  height: 57px;
  width: 100%;
  background-color: #b6b6b6;
  display: flex;
  align-items: center;
  padding-top: 14px;
  padding-left: 10px;
  font-size: 17px;
}
.bottomBar .specialSign {
  font-weight: bold;
  font-size: 20px;
}
.bottomBar .bottomBarColor {
  color: #d58e32;
}