@keyframes bounce {
  0%, 50%, 100% {
    opacity: 1;
  }
  25%, 75% {
    opacity: 0;
  }
}
@keyframes blink {
  0%, 50%, 100% {
    opacity: 1;
  }
  25%, 75% {
    opacity: 0;
  }
}
:export {
  fadeTime: 500ms;
}

.brandsSection {
  position: relative;
  height: 120px;
  display: flex;
  margin-top: 20px;
  margin-bottom: 30px;
}

.brandsWrapper {
  display: flex;
  margin: 0 auto;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  border-top: 1px solid #dfdfdf;
  border-bottom: 1px solid #dfdfdf;
}

.brandbutton {
  width: 35px;
  background-color: #2a2a2a;
  text-align: center;
  margin-top: 15px;
  margin-bottom: 15px;
}