@keyframes bounce {
  0%, 50%, 100% {
    opacity: 1;
  }
  25%, 75% {
    opacity: 0;
  }
}
@keyframes blink {
  0%, 50%, 100% {
    opacity: 1;
  }
  25%, 75% {
    opacity: 0;
  }
}
:export {
  fadeTime: 500ms;
}

.main, .small {
  background-color: #2a2a2a;
  color: #ffffff;
  font-size: 16px;
  line-height: 24px;
  font-weight: 600;
  padding: 10px 30px;
}
.main:not(.noHover):hover, .small:not(.noHover):hover {
  color: #ffffff !important;
  background-color: #d58e32;
  text-decoration: none;
}

.small {
  font-size: 13px;
  line-height: 24px;
  padding: 5px 10px;
  font-weight: 400;
}

.outline {
  display: inline-block;
  padding: 5px 10px;
  border: 1px solid #2a2a2a;
  color: #2a2a2a;
  font-size: 13px;
  line-height: 22px;
}
.outline:not(.noHover):hover {
  animation: bounce 2s ease 0s 1 normal forwards;
  background-color: #2a2a2a;
  color: #ffffff;
}

.favorite,
.compare {
  color: #d58e32;
  border: 1px solid #d58e32;
}