@keyframes bounce {
  0%, 50%, 100% {
    opacity: 1;
  }
  25%, 75% {
    opacity: 0;
  }
}
@keyframes blink {
  0%, 50%, 100% {
    opacity: 1;
  }
  25%, 75% {
    opacity: 0;
  }
}
:export {
  fadeTime: 500ms;
}

.root {
  display: flex;
  margin-left: 28px;
  max-width: 1130px;
}

.panelBar {
  margin-bottom: 15px;
  position: relative;
}
.panelBar :global(.row) > * {
  border-bottom: 4px solid #e2e2e2;
}
.panelBar .heading h3 {
  position: relative;
  color: #d58e32;
  text-transform: uppercase;
  font-size: 20px;
  line-height: 38px;
  margin: 0;
  letter-spacing: 1px;
  z-index: 2;
}
.panelBar .heading::before {
  content: "";
  position: absolute;
  bottom: -4px;
  left: 0;
  width: 45px;
  border-bottom: 4px solid #d58e32;
}
.panelBar .dots {
  position: absolute;
  right: 0;
  top: 0;
  z-index: 1;
}
.panelBar .dots ul {
  margin: 0;
  padding: 0 0 0 1rem;
  list-style: none;
  background-color: #ffffff;
  transform: translateY(15px);
}
.panelBar .dots ul li {
  display: inline-block;
  margin-left: 0.5rem;
}
.panelBar .dots ul li a {
  display: block;
  width: 13px;
  height: 13px;
  border-radius: 6px;
  background-color: #e1e1e1;
  font-size: 0;
}
.panelBar .dots ul li a.active, .panelBar .dots ul li a:hover {
  background-color: #d58e32;
}