@keyframes bounce {
  0%, 50%, 100% {
    opacity: 1;
  }
  25%, 75% {
    opacity: 0;
  }
}
@keyframes blink {
  0%, 50%, 100% {
    opacity: 1;
  }
  25%, 75% {
    opacity: 0;
  }
}
:export {
  fadeTime: 500ms;
}

.root {
  background-color: #363636;
}
.root :global(.container) > :global(.row) {
  height: 145px;
}
.root a img {
  height: 100%;
  text-align: center;
}
.root .phoneNumber p {
  margin: 0;
  font-size: 17px;
  line-height: 42px;
  color: rgb(216, 216, 216);
  font-weight: 500;
}
.root .phoneNumber p .icon {
  font-size: 32px;
  color: #d58e32;
  vertical-align: middle;
  margin-top: -3px;
  margin-right: 0.5rem;
}
.root .cart .cartBox {
  display: inline-block;
  position: relative;
  color: #ffffff;
}
.root .cart .cartBox .cartIcon {
  background-color: #d58e32;
  width: 55px;
  height: 50px;
  border-radius: 3px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 20px;
}
.root .cart .cartBox .cartIcon .icon {
  margin-right: 5px;
}
.root .cart .cartBox .cartCounter {
  width: 28px;
  height: 27px;
  border-radius: 14px;
  background-color: #363636;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 14px;
  color: rgb(224, 227, 237);
  position: absolute;
  top: 50%;
  right: 0;
  transform: translate(50%, -50%);
}
.root .cart .cartBox:hover .cartIcon {
  background-color: #dea55c;
}

/* Media */
@media (max-width: 767px) {
  .root a img {
    height: 50px;
    order: 0;
  }
  .root .phoneNumber {
    order: 1;
    margin-top: 40px;
  }
  .root .phoneNumber p {
    font-size: 14px;
    text-align: right;
  }
  .root .phoneNumber p .icon {
    font-size: 20px;
  }
}