@keyframes bounce {
  0%, 50%, 100% {
    opacity: 1;
  }
  25%, 75% {
    opacity: 0;
  }
}
@keyframes blink {
  0%, 50%, 100% {
    opacity: 1;
  }
  25%, 75% {
    opacity: 0;
  }
}
:export {
  fadeTime: 500ms;
}

.root {
  border: 1px solid #292929;
  display: inline-flex;
  height: 45px;
  color: #2a2a2a;
}
.root .category {
  border-right: 1px solid #292929;
  display: flex;
  align-items: center;
  position: relative;
}
.root .category .icon:first-child {
  color: #d58e32;
  position: absolute;
  left: 10px;
  z-index: 0;
}
.root .category .icon:last-child {
  position: absolute;
  right: 10px;
  z-index: 0;
}
.root .category .icon:last-child:hover {
  color: #d58e32;
}
.root .category ul {
  background-color: transparent !important;
  cursor: pointer;
  padding: 15px 30px 5px 35px;
  position: relative;
  display: block;
  height: 35px;
  font-size: 14px;
}
.root .category ul div {
  padding-bottom: 10px;
}
.root .category ul li {
  display: none;
  cursor: pointer;
  font-weight: 400;
  font-size: 14px;
  padding: 1px 0px 1px 7px;
}
.root .category ul li:hover {
  background-color: orange;
}
.root .category ul:hover li {
  display: block;
}
.root .category option {
  font-size: 14px;
  display: block;
  height: 35px;
  font-size: 14px;
}
.root .category option div {
  padding-bottom: 10px;
}
.root .category option li {
  display: none;
  cursor: pointer;
  font-weight: 400;
  font-size: 14px;
  padding: 1px 0px 1px 7px;
}
.root .category option:hover {
  background-color: orange;
}
.root .category option:hover li {
  display: block;
}
.root .category option:hover li {
  display: block;
}
@media (max-width: 600px) {
  .root .category option {
    font-size: 11px;
    width: 163px;
  }
}
.root .searchField {
  display: flex;
  align-items: center;
  position: relative;
}
.root .searchField input {
  border: 0;
  padding-left: 10px;
  font-size: 14px;
  outline: none;
  opacity: 0.5;
}
.root .searchField input::placeholder {
  color: #2a2a2a;
}
@media (max-width: 600px) {
  .root .searchField input {
    font-size: 11px;
    width: 110px;
  }
}
.root .searchField button {
  border: 0;
  background-color: transparent;
  outline: none;
}
.root .searchField button :hover {
  color: #d58e32;
}