@keyframes bounce {
  0%, 50%, 100% {
    opacity: 1;
  }
  25%, 75% {
    opacity: 0;
  }
}
@keyframes blink {
  0%, 50%, 100% {
    opacity: 1;
  }
  25%, 75% {
    opacity: 0;
  }
}
:export {
  fadeTime: 500ms;
}

.registerBox {
  position: relative;
  height: 1000px;
}

.innerBox {
  position: absolute;
  width: 540px;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  height: 90%;
}