@keyframes bounce {
  0%, 50%, 100% {
    opacity: 1;
  }
  25%, 75% {
    opacity: 0;
  }
}
@keyframes blink {
  0%, 50%, 100% {
    opacity: 1;
  }
  25%, 75% {
    opacity: 0;
  }
}
:export {
  fadeTime: 500ms;
}

.root {
  box-shadow: 2px 3.464px 6px rgba(1, 2, 2, 0.2);
  background-color: #ffffff;
}
.root :global(.container) > :global(.row) {
  height: 100%;
}
.root .bars {
  width: 50px;
  height: 50px;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #ffffff;
  border: none;
}
.root .icon {
  color: #d58e32;
  display: none;
}
@media (max-width: 414px) {
  .root .icon {
    padding-left: 0;
    display: block;
  }
}
.root .menu {
  display: flex;
  align-self: stretch;
}
@media (max-width: 414px) {
  .root .menu {
    padding-left: 0;
    padding-right: 0;
    width: 100%;
  }
}
.root .menu ul {
  margin: 0;
  padding: 0;
  display: flex;
}
.root .menu ul li {
  list-style: none;
  display: flex;
  align-items: stretch;
}
.root .menu a {
  color: #2a2a2a;
  text-transform: uppercase;
  font-size: 12px;
  padding: 1rem;
  text-decoration: none;
  display: flex;
  align-items: center;
  border-top: 4px solid #ffffff;
  font-weight: 500;
  letter-spacing: 1px;
}
.root .menu a:hover, .root .menu a.active {
  background-color: #d58e32;
  color: #ffffff;
  border-color: #d58e32;
}
.root .menu a.active {
  border-color: #2a2a2a;
}
@media screen and (max-width: 414px) {
  .root .menu a {
    display: block;
    width: 100%;
    text-align: center;
  }
  .root .menu ul {
    display: none;
    flex-direction: column;
    width: 100%;
    padding-left: 15px;
  }
  .root .menu ul.active {
    display: block;
  }
  .root .menu li {
    width: 100%;
    justify-content: center;
  }
  .root .menu .searchbar {
    padding-left: 0;
  }
}