@keyframes bounce {
  0%, 50%, 100% {
    opacity: 1;
  }
  25%, 75% {
    opacity: 0;
  }
}
@keyframes blink {
  0%, 50%, 100% {
    opacity: 1;
  }
  25%, 75% {
    opacity: 0;
  }
}
:export {
  fadeTime: 500ms;
}

.stickyBar {
  width: 100%;
  height: 215px;
  background-color: #dfdfdf;
  position: fixed;
  z-index: 100;
  bottom: 0;
  overflow: hidden;
}
.stickyBar .photos {
  height: 60%;
  display: flex;
  justify-content: center;
}
.stickyBar .photos .photoContainer {
  position: relative;
  margin: 5px;
  flex-basis: 18%;
  border: 5px solid #676767;
  border-radius: 5px;
}
.stickyBar .photos .photoContainer:hover .removeIcon {
  visibility: visible;
}
.stickyBar .photos .photoContainer:hover .photo {
  opacity: 0.8;
}
.stickyBar .photos .photoContainer .photo {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.stickyBar .photos p {
  margin-bottom: 0;
}
.stickyBar .photos p.oldPrice {
  text-decoration: line-through;
}
.stickyBar .photos .textBox {
  margin-top: 2px;
}
.stickyBar .removeIconBox {
  visibility: hidden;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.stickyBar .removeIconBox .removeIcon {
  font-size: 7rem;
  opacity: 0.7;
  color: #d58e32;
}
.stickyBar .compareButton {
  border: 2px solid #d58e32;
  background-color: #dfdfdf;
  border-radius: 5px;
  color: #d58e32;
  height: 40px;
  margin-left: 10px;
  position: relative;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.stickyBar .compareButton:hover {
  background-color: #d58e32;
  color: #dfdfdf;
}

@media (max-width: 1100px) {
  .stickyBar .removeIconBox .removeIcon {
    font-size: 5rem;
  }
  .stickyBar .photos .photoContainer {
    margin: 2px;
    flex-basis: 20%;
  }
  .stickyBar .photos .textBox p {
    font-size: 0.8rem;
  }
  .stickyBar .compareButton {
    margin-left: 1px;
  }
}
@media (max-width: 650px) {
  .stickyBar {
    height: 240px;
  }
  .stickyBar .photos {
    height: 40%;
  }
  .stickyBar .photos .photoContainer {
    margin: 1px;
    flex-basis: 25%;
  }
  .stickyBar .compareButton {
    position: absolute;
    top: 90%;
    height: 40px;
    margin-left: 1px;
  }
}
@media (max-width: 450px) {
  .stickyBar {
    height: 250px;
  }
  .stickyBar .photos {
    margin-top: 5px;
    flex-wrap: wrap;
    height: 75%;
  }
  .stickyBar .photos .photoContainer {
    height: 50%;
    flex-basis: 48%;
  }
  .stickyBar .textBox {
    user-select: none;
    position: absolute;
    top: 0;
    color: #d58e32;
    font-weight: 900;
    -webkit-text-stroke: 0.9px #2a2a2a;
    line-height: 1;
  }
}