@keyframes bounce {
  0%, 50%, 100% {
    opacity: 1;
  }
  25%, 75% {
    opacity: 0;
  }
}
@keyframes blink {
  0%, 50%, 100% {
    opacity: 1;
  }
  25%, 75% {
    opacity: 0;
  }
}
:export {
  fadeTime: 500ms;
}

.brandElement {
  padding: 14px;
}

@media (max-width: 1199px) {
  .brandElement {
    padding: 10px;
  }
}
@media (max-width: 991px) {
  .brandElement {
    padding: 6px;
  }
}
@media (max-width: 767px) {
  .brandElement {
    padding: 4px;
  }
}
@media (max-width: 511px) {
  .brandElement {
    padding: 2px;
  }
}