@keyframes bounce {
  0%, 50%, 100% {
    opacity: 1;
  }
  25%, 75% {
    opacity: 0;
  }
}
@keyframes blink {
  0%, 50%, 100% {
    opacity: 1;
  }
  25%, 75% {
    opacity: 0;
  }
}
:export {
  fadeTime: 500ms;
}

.container {
  margin-bottom: 80px;
}

.column {
  width: 550px;
  height: 420px;
  position: relative;
  padding: 0;
  color: white;
}
.column img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  border-radius: 3px;
}
.column .firstWrapper {
  position: absolute;
  bottom: 45px;
  left: 53px;
}
.column h2 {
  font-size: 36px;
  font-weight: 400;
  margin: 0;
}
.column h1 {
  margin: 0;
  font-size: 60px;
}
.column p {
  width: 160px;
  font-family: "Poppins - Semi Bold";
  font-size: 50px;
  font-weight: 400;
  padding: 5px;
  text-align: center;
  border: 5px solid white;
}

.column::before {
  display: block;
  content: "";
  position: absolute;
  width: 90%;
  height: 90%;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: grey;
  opacity: 0.7;
  border-radius: 2px;
}

.column1 {
  border-radius: 3px;
  position: relative;
  width: 550px;
  height: 195px;
}
.column1 img {
  border-radius: 3px;
  position: absolute;
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.column1 .secondWrapper {
  text-align: center;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(10%, -50%);
  font-size: 30px;
}
.column1 .secondWrapper h1 {
  font-size: 30px;
  display: inline-block;
  font-family: "Poppins Light";
}
.column1 .secondWrapper h1.boldText {
  font-weight: 600;
  font-family: Poppins;
  padding-right: 10px;
}
.column1 .secondWrapper h5 {
  margin: 0;
  font-size: 18px;
  letter-spacing: 3.6px;
}
.column1 .secondWrapper p {
  margin: 0;
  color: orange;
  font-family: "Poppins - Semi Bold";
  font-size: 36px;
  line-height: 54px;
  font-weight: 600;
}

.column2 {
  border-radius: 3px;
  position: relative;
  width: 555px;
  height: 195px;
  padding-top: 30px;
}
.column2 img {
  border-radius: 3px;
  position: absolute;
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.column2 .thirdWrapper {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translateY(0%);
  color: black;
  font-size: 24px;
  font-weight: 300;
}
.column2 .thirdWrapper .Normal {
  padding-left: 10px;
  display: inline-block;
  margin: 0;
  font-family: "Poppins Light";
  color: orange;
}
.column2 .thirdWrapper .Bold {
  display: inline-block;
  margin: 0;
  font-weight: 800;
  color: orange;
}
.column2 .thirdWrapper .Info {
  margin: 0;
  color: black;
  font-size: 18px;
  transform: translateX(20px);
}

/*Media*/
@media (max-width: 768px) {
  .container {
    margin-bottom: -15%;
  }
  .column {
    width: 100%;
  }
  .column h2 {
    font-size: 30px;
  }
  .column h1 {
    font-size: 50px;
  }
  .column p {
    width: 140px;
    font-size: 45px;
  }
  .twoColumn {
    flex-direction: row;
  }
  .column1 {
    width: 50%;
    height: 325px;
    text-align: center;
    right: 28%;
  }
  .column2 {
    width: 50%;
    text-align: center;
    left: 28%;
    bottom: 56%;
  }
  .column2 .Info {
    margin: 0;
    color: black;
    font-size: 18px;
    font-weight: 550;
    transform: translateX(20px);
  }
}
@media (max-width: 425px) {
  .column {
    width: 100%;
  }
  .column h2 {
    font-size: 25px;
  }
  .column h1 {
    font-size: 40px;
  }
  .column p {
    width: 120px;
    font-size: 38px;
  }
  .twoColumn {
    flex-direction: row;
  }
  .column1 {
    width: 50%;
    height: 325px;
    text-align: center;
    right: 28%;
  }
  .column2 {
    width: 50%;
    text-align: center;
    left: 28%;
    bottom: 56%;
  }
  .column2 .Info {
    margin: 0;
    color: black;
    font-size: 15px;
    font-weight: 550;
    transform: translateX(20px);
  }
}